.AppHeader{
    color: #fff;
}
.SearchPanel{
    margin-bottom: 15px;
}
.TodoListItem {
     font-weight: 500;
 }

.TodoListItem.done {
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
}

.TodoListItem.important {
    font-weight: bold;
    color: #ff000b;
}
.TodoList{
    margin-top: 15px;
}
.TodoList button{
    margin: 0 5px;
}
.TodoList li{
    margin: 3px 0;
    border: 1px solid transparent;
    -webkit-transition: ease .3s;
    transition: ease .3s;
    cursor: pointer;
}
.TodoList li:hover{
    border: 1px solid #000;
}
.ItemAddForm{
    margin-top: 15px;
}
.ItemAddForm input{
    margin-right: 5px;
}
